h1 {
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
  text-align: center;
  font-weight: 100;
  background-color: white;
  margin: 0;
  padding: 20px;
  color: #004d40;
  -webkit-box-shadow: 0 10px 20px rgba(0, 77, 64, 0.25), 0 6px 6px rgba(0, 77, 64, 0.22);
          box-shadow: 0 10px 20px rgba(0, 77, 64, 0.25), 0 6px 6px rgba(0, 77, 64, 0.22);
  text-shadow: 0px 0px 1px rgba(29, 233, 182, 1);
  -webkit-transition: 1.5s;
  -o-transition: 1.5s;
  transition: 1.5s;

}

text {
  font-size: 25px;
  font-weight: bold;
}

form {
  padding-top: 35px;
  padding-bottom: 35px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 50px;
  -webkit-transition: 1.5s;
  -o-transition: 1.5s;
  transition: 1.5s
}

.form-wrapper {
  display: -ms-flexbox;
  display: flex;
  border-radius: 100px;
  -webkit-box-shadow: 0 10px 20px rgba(0, 77, 64, 0.25), 0 6px 6px rgba(0, 77, 64, 0.22);
          box-shadow: 0 10px 20px rgba(0, 77, 64, 0.25), 0 6px 6px rgba(0, 77, 64, 0.22);
}

input {
  border: none;
  text-align: center;
  border-radius: 100px;
  padding: 25px;
  font-family: inherit;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 340px;
  outline: none;
  font-size: 18px;
  text-transform: uppercase;
}

input::-webkit-input-placeholder {
  color: grey;
  text-transform: initial;
}

input:-ms-input-placeholder {
  color: grey;
  text-transform: initial;
}

input::-ms-input-placeholder {
  color: grey;
  text-transform: initial;
}

input::placeholder {
  color: grey;
  text-transform: initial;
}

form button {
  outline: none;
  border: none;
  background-color: white;
  color: white;
  border-top-right-radius: 100px;
  border-bottom-right-radius: 100px;
  font-size: 24px;
  font-family: inherit;
  width: 50px;
  font-weight: bold;
  padding: 0;
  margin-left: -50px;
  -webkit-transition: color ease-in-out 1s;
  -o-transition: color ease-in-out 1s;
  transition: color ease-in-out 1s;
}

form button:hover, .demo:hover {
  color: rgb(29, 233, 182);
  cursor: pointer;
  outline: none;
  -webkit-transition: ease-in-out 0.5s;
  -o-transition: ease-in-out 0.5s;
  transition: ease-in-out 0.5s;
}

.form.demonstration {
  /* transition: 1.5s; */
  cursor: default;
  -webkit-transform: translateY(-300px);
      -ms-transform: translateY(-300px);
          transform: translateY(-300px)
}
h1.demonstration {
  z-index: 100;
  -webkit-transition: 1.5s;
  -o-transition: 1.5s;
  transition: 1.5s;
  -webkit-transition-delay: 0.3s;
       -o-transition-delay: 0.3s;
          transition-delay: 0.3s;
  color: rgb(29, 233, 182);
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
  background-color: black;
  margin-bottom: -100px
}
.demo.demonstration {
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0)
}

form button:active, .demo:active {
  -webkit-box-shadow: inset 1px 0px 3px 0px rgba(38, 50, 56, 1);
          box-shadow: inset 1px 0px 3px 0px rgba(38, 50, 56, 1);
  outline: none;
  -webkit-transition: ease-in-out 0.5s;
  -o-transition: ease-in-out 0.5s;
  transition: ease-in-out 0.5s;

}

.demo {
  outline: none;
  display: block;
  margin: 0 auto;
  margin-top: -20px;
  border: none;
  background-color: white;
  color: rgb(29, 233, 182);
  border-radius: 100px;
  font-weight: bold;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
          box-shadow: 0 0 0 30px white inset;
}

.svgWrapper {
  padding-top: 35px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

@media (max-width: 1000px) {
  svg {
    width: 860px;
  }
  #svg_0 {
    -webkit-transform: scale(0.91);
        -ms-transform: scale(0.91);
            transform: scale(0.91);
  }
  #svg_3,
  #svg_4,
  #svg_5,
  #svg_6 {
    -webkit-transform: scale(0.9);
        -ms-transform: scale(0.9);
            transform: scale(0.9);
  }
}

@media (max-width: 600px) {
  svg {
    width: 360px;
  }
  #svg_3,
  #svg_4,
  #svg_5,
  #svg_6 {
    -webkit-transform: scale(0.37);
        -ms-transform: scale(0.37);
            transform: scale(0.37);
  }
  .empty {   
    font-size: 20px
  }
  .error {
    font-size: 12px;
  }
  #svg_0 {
    -webkit-transform: scale(0.6);
        -ms-transform: scale(0.6);
            transform: scale(0.6);
    -webkit-transform: translateX(-300px);
        -ms-transform: translateX(-300px);
            transform: translateX(-300px);
  }
}



