rect,
path {
  opacity: 1;
  -webkit-animation: appear 1s;
          animation: appear 1s;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
.black {
  fill: black;
}
.grey {
  fill: grey;
}
.white {
  fill: white;
}

@-webkit-keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



